import React, { useState } from 'react'

import { Stack, Typography } from '@mui/material'

const InfoPost = ({ title, iconURL, ...props }) => {
    const [flagVisible, setFlagVisible] = useState(false)

    const styles = {
        ...defaultStyles,

        container: {
            ...defaultStyles.container,
            background: `linear-gradient(180deg, rgba(0, 0, 0, 0.126) 0%, rgba(229, 105, 124, 0.7) 100%), url(${iconURL}) no-repeat center center / cover`
        }
    }

    return (
        <Stack
            sx={styles.container}
            {...props}
        >
            <Flag title='Update 2022' visible={flagVisible} />
            <Typography sx={styles.title}>
                {title}
            </Typography>
        </Stack>
    )
}

const Flag = ({ title, visible }) => visible ?
    <Typography display='block' sx={defaultStyles.flag}>{title}</Typography> :
    <></>

export default InfoPost

const defaultStyles = {
    container: {
        position: 'relative',
        width: '170px',
        height: '180px',
        borderRadius: '10px',
        paddingTop: '20px',
        '&:hover': {
            cursor: 'pointer'
        }
    },
    flag: {
        backgroundColor: '#e5697c',
        color: '#fff',
        fontWeight: '700'
    },
    title: {
        position: 'absolute',
        color: '#fff',
        fontWeight: '700',
        left: '14px',
        right: '14px',
        bottom: '14px',
    },
}