import React from 'react'

import { Stack, Typography } from '@mui/material'

import { useNavigate } from 'react-router-dom'

const Post = ({ title, icon, url, ...props }) => {
    const navigate = useNavigate()

    const goTo = () => {
        if (url) navigate(url)
    }

    return (
        <Stack
            sx={styles.container}
            justifyContent='center'
            alignItems='center'
            onClick={goTo}
            {...props}
        >
            <Typography sx={styles.title}>
                {title}
            </Typography>
            {icon}
        </Stack>
    )
}

export default Post

const styles = {
    container: {
        position: 'relative',
        width: '170px',
        height: '140px',
        backgroundColor: '#e5697c',
        border: '2px solid #e5697c',
        transitionDuration: '250ms',
        color: '#fff',
        borderRadius: '10px',
        '&:hover': {
            cursor: 'pointer',
            backgroundColor: '#fff',
            color: '#e5697c'
        }
    },
    title: {
        textAlign: 'center',
        fontWeight: '700',
        zIndex: 1,
        fontSize: '1rem'
    }
}