import React from 'react'

import { Typography } from '@mui/material'

import Article from 'components/Layout/Article'

const WrittenDecryption = () => {
  return (
    <Article title=' '>
      <Typography variant='p' sx={styles.content}>
            ,   :        ,       .
        <br /><br />
            ,        ,        .      ,  ,                   ,      .
        <br /><br />
             ,  ,    -         ,  .
      </Typography>
    </Article>
  )
}

export default WrittenDecryption

const styles = {
  content: {
    fontSize: '1.2rem',
    fontWeight: '600',
    lineHeight: '30px',
    textAlign: 'justify'
  }
}