import React, { useState } from 'react'

import { Box } from '@mui/material'

import BellURL from 'public/img/header/bell.svg'
import BellActiveURL from 'public/img/header/bell-active.svg'

import 'styles/header.css'

const Notifications = () => {
  const [active, setActive] = useState(true);

  return (
    <Box className='notifications mr' sx={{ '&:hover': { cursor: 'pointer' } }}>
      {active ? (
        <img className='bell' src={BellURL} alt='notifications' />
      ) : (
        <img className='bell' src={BellActiveURL} alt='notifications' />
      )}
    </Box>
  )
}

export default Notifications