import {
    SpaOutlined,
    AutoStoriesOutlined,
    RestoreOutlined,
    NoteAltOutlined,
    LocalHospitalOutlined,
    LiveHelpOutlined
} from '@mui/icons-material'

import PostInfo1 from 'public/img/common/posts/post-info-1.jpg'
import PostInfo2 from 'public/img/common/posts/post-info-2.jpg'
import PostInfo3 from 'public/img/common/posts/post-info-3.jpg'
import PostInfo4 from 'public/img/common/posts/post-info-4.jpg'
import PostInfo5 from 'public/img/common/posts/post-info-5.jpg'
import PostInfo6 from 'public/img/common/posts/post-info-6.jpg'

const styles = {
    postIcon: {
        position: 'absolute',
        width: '100%',
        height: '100%',
        opacity: 0.15,
    },
}

export const posts = [
    <SpaOutlined style={styles.postIcon} />,
    <AutoStoriesOutlined style={styles.postIcon} />,
    <RestoreOutlined style={styles.postIcon} />,
    <NoteAltOutlined style={styles.postIcon} />,
    <LocalHospitalOutlined style={styles.postIcon} />,
    <LiveHelpOutlined style={styles.postIcon} />,
]

export const infoPosts = [
    PostInfo1,
    PostInfo2,
    PostInfo3,
    PostInfo4,
    PostInfo5,
    PostInfo6,
]


export const formatCountdown = (days = 0, hours = 0, minutes = 0, seconds = 0) => {
    if (days !== 0)
        return `${days}  | ${hours}  | ${minutes}  | ${seconds} `
    if (hours !== 0)
        return `${hours}  | ${minutes}  | ${seconds} `
    if (minutes !== 0)
        return `${minutes}  | ${seconds} `
    if (seconds !== 0)
        return `${seconds} `

    return ''
}