import React, { useState } from 'react'

import { Typography, Stack, Button } from '@mui/material'
import Countdown from 'react-countdown'

import Modal from 'components/Modal'

import BackgroundURL from 'public/img/common/background.jpg'

import { formatCountdown } from 'utils/post'

const MainPost = () => {
    const styles = {
        ...defaultStyles,

        container: {
            ...defaultStyles.container,
            background: `linear-gradient(180deg, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.6) 100%), url(${BackgroundURL}) no-repeat center center / cover`,
            // backgroundImage: `url(${BackgroundURL}), linear-gradient(#000, #000)`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
        }
    }

    // modal
    const [open, setOpen] = useState(false)
    const handleOpen = () => setOpen(true)
    const handleClose = () => setOpen(false)

    // countdown
    const renderer = ({ days, hours, minutes, seconds, completed }) => {
        if (completed)
            return <Typography variant='h5' textAlign='center'>  :)</Typography>
        else
            return (
                <Typography variant='h5' textAlign='center'>
                    {formatCountdown(days, hours, minutes, seconds)}
                </Typography>
            )
    }

    return (
        <Stack style={styles.container} direction='column' justifyContent='space-between' alignItems='flex-start'>
            <Typography variant='h5' sx={styles.title}>
                
                <br />
                Digital  
            </Typography>
            <Button
                variant='contained'
                sx={styles.btn}
                onClick={handleOpen}
            ></Button>
            <Modal
                open={open}
                onClose={handleClose}
                title='!    '
                description=' :'
            >
                <Countdown date={new Date('2022-10-27')} renderer={renderer} />
            </Modal>
        </Stack >
    )
}

export default MainPost

const defaultStyles = {
    container: {
        position: 'relative',
        height: '12rem',
        borderRadius: '15px',
        padding: '2rem 3rem',
        marginBottom: '2rem'
    },
    title: {
        color: '#fff',
        fontWeight: '700',
        textTransform: 'uppercase',
    },
    btn: {
        bottom: '0',
        backgroundColor: '#e5697c',
        border: '2px solid #e5697c',
        fontWeight: '700',
        padding: '.2rem 2rem',
        textTransform: 'none',
        borderRadius: '10px',
        '&:hover': {
            backgroundColor: '#fff',
            color: '#e5697c',
        }
    },
}