import React, { useMemo } from 'react'

import { Stack } from '@mui/material'

import Post from 'components/Post'

import { posts as postsBG } from 'utils/post'

const PostsList = () => {
    const posts = useMemo(() => [
        { title: ' ', url: '/about-consilium' },
        { title: ' ', url: '/user-instruction' },
        { title: ' ' },
        { title: ' ', url: '/written-decryption' },
        { title: '-    ', url: '/online-consultation' },
        { title: '  . ', url: '/technical-support' },
    ], [])

    return (
        <Stack direction='row' justifyContent='space-around'>
            {posts.map((post, id) => (
                <Post
                    key={id}
                    title={post.title}
                    icon={postsBG[id]}
                    url={post.url}
                />))}
        </Stack>
    )
}

export default PostsList