import React from 'react'

import { Routes, Route } from 'react-router-dom'

import AuthorizedLayout from 'components/Layout/Authorized'

import Patient from 'pages/Patient'
import AboutConsilium from 'pages/Patient/articles/AboutConsilium'
import UserInstruction from 'pages/Patient/articles/UserInstruction'
import WrittenDecryption from 'pages/Patient/articles/WrittenDecryption'
import OnlineConsultation from 'pages/Patient/articles/OnlineConsultation'
import TechnicalSupport from 'pages/Patient/articles/TechnicalSupport'


const Root = () => {
    return (
        <Routes>
            <Route element={<AuthorizedLayout />}>
                <Route path='/' element={<Patient />} />
                <Route path='/about-consilium' element={<AboutConsilium />} />
                <Route path='/user-instruction' element={<UserInstruction />} />
                <Route path='/written-decryption' element={<WrittenDecryption />} />
                <Route path='/online-consultation' element={<OnlineConsultation />} />
                <Route path='/technical-support' element={<TechnicalSupport />} />
            </Route>
        </Routes>
    )
}

export default Root