import React from 'react'

import { Stack, Typography } from '@mui/material'

const Article = ({ title, children }) => {
    return (
        <Stack sx={styles.container}>
            <Typography variant='h3' sx={styles.title}>{title}</Typography>
            <br />
            {children}
        </Stack>
    )
}

export default Article

const styles = {
    title: {
        color: '#e5697c',
        fontWeight: '700'
    }
}