import React from 'react'

import { Stack, Typography } from '@mui/material'

import { ReactComponent as Maple } from 'public/img/logo/maple.svg'

import { useNavigate, useLocation } from 'react-router-dom'

const Logo = () => {
    const location = useLocation()
    const navigate = useNavigate()

    // navigate back to the main page
    const goTo = () => {
        if (location.pathname != '/')
            navigate('/')
    }

    return (
        <Stack
            direction='row'
            alignItems='center'
            sx={{
                color: '#e5697c',
                '&:hover': {
                    cursor: 'pointer'
                }
            }}
            onClick={goTo}
        >
            <Maple className='maple' />
            <Typography variant='h4' sx={{ fontWeight: '800' }}>Clarity</Typography>
        </Stack >
    )
}

export default Logo