import React, { useMemo } from 'react'

import { Stack, Divider } from '@mui/material'

import InfoPost from 'components/Post/InfoPost'

import { infoPosts as infoPostsBG } from 'utils/post'

const InfoPostsList = () => {
    const posts = useMemo(() => [
        { title: '  ' },
        { title: '-  ' },
        { title: '    ' },
        { title: '  ' },
        { title: '  ' },
        { title: '    10-11' },
    ], [])

    return (
        <>
            <Divider style={styles.divider} />
            <Stack direction='row' justifyContent='space-around'>
                {posts.map((post, id) => <InfoPost key={id} title={post.title} iconURL={infoPostsBG[id]} />)}
            </Stack>
        </>
    )
}

export default InfoPostsList

const styles = {
    divider: {
        border: 'none',
        height: '3px',
        margin: '2rem 0',
        /* Set the hr color */
        color: '#d6d6d6', /* old IE */
        backgroundColor: '#d6d6d6' /* Modern Browsers */
    }
}